export const CatalogView = () => {
    // const { classes } = props;
    return(
        <React.Fragment>
        <div style={{ marginRight: '5%', marginLeft: '5%' }}>
            <h3> Catalog Produk </h3>
            <main>
                <div>
                    {/* End hero unit */}
                    <Grid container spacing={40}>
                        { products.map((product) =>
                            <Grid item key={product.id} sm={6} md={4} lg={4}>
                            <Card>
                              <CardMedia
                                style={{ paddingTop: '56.25%' }}
                                image={product.image}
                                title="Image title"
                              />
                              <CardContent>
                                <Typography gutterBottom variant="h5" component="h2">
                                  {product.label}
                                </Typography>
                                <Typography>
                                  {product.content}
                                </Typography>
                              </CardContent>
                              <CardActions>
                                <Link to={`/product/`+product.id}>
                                  <Button variant="contained" size="small" color="primary">
                                    View
                                  </Button>
                                </Link>
                                <Button variant="contained" size="small" color="primary">
                                  Edit
                                </Button>
                              </CardActions>
                            </Card>
                          </Grid> 
                        )}
                    </Grid>
                </div>
            </main>
            {/* Footer */}
                <footer>
                  <Typography variant="h6" align="center" gutterBottom>
                    Footer
                  </Typography>
                  <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
                    Something here to give the footer a purpose!
                  </Typography>
                </footer>
            {/* End footer */}
        </div>
        </React.Fragment>
    )
}

export const ProductView = ({ match }) => {
    const product = products.find(item => item.id === match.params.id);
    console.log(products, match.params.id);
    return(
        <div>
            <h3> Product ID: {match.params.id} </h3>
            <h3> {product.id} - {product.label} </h3>
            <Link to={`/catalog`}> Back to Catalog </Link>
        </div>
    )
}